$WHITE: #FFFFFF;
$GREY: #5C5C5C;
$LIGHT_GREY: #989898;

.line {
	width: 100%;
	height: 2px;
	background-color: $LIGHT_GREY;

	@include rmin(1023) {
		margin-top: 40px; }

	@include rmin(675) {
		margin-top: 20px; }

	@include rmin(321) {
		margin-top: 15px; } }

.footer {
	width: 100%;
	height: 70px;

	&-container {
		position: relative;
		margin: 0 auto;

		@include rmin(1023) {
			width: 840px; }

		@include rmin(675) {
			width: 640px; }

		@include rmin(321) {
			width: 320px; } }

	&-networks {
		position: absolute;
		@include rmin(1023) {
			top: -10px;
			left: 370px; }

		@include rmin(675) {
			top: -20px;
			left: 155px; }

		@include rmin(321) {
			top: -5px;
			left: 50px; } } }


.list-item {
	&__facebook {
		width: 13px;
		height: auto;

		&:hover {
			opacity: 0.5; } }

	&__google_plus {
		width: 30px;
		height: auto;

		&:hover {
			opacity: 0.5; } }

	&__instagram {
		width: 23px;
		height: auto;

		&:hover {
			opacity: 0.5; } } }

.footer-rights {
	position: absolute;
	font-size: 12px;
	color: $LIGHT_GREY;
	@include rmin(1023) {
		top: 30px; }

	@include rmin(675) {
		text-align: center;
		width: 100%;
		top: 45px; }

	@include rmin(321) {
		text-align: center;
		width: 100%;
		top: 40px; } }

