    grid-template-areas: "grid-item grid-item grid-item" "grid-item grid-item grid-item";
    grid-row-gap: 55px;
    margin-top: 55px;
    grid-template-columns: 250px 250px 250px;
    grid-column-gap: 30px;
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media only screen and (min-width: 675px) {
