$ORANGE: #ff8400;
$SPACE_GREY: #343434;
$GREY: #5C5C5C;

.about {
	width: 100%;

	@include rmin(1023) {
		height: 630px; }

	@include rmin(675) {
		height: 620px; }

	@include rmin(321) {
		height: 800px; }

	&-container {
		height: inherit;
		margin: 0 auto;
		padding-top: 45px;
		padding-bottom: 0px;

		@include rmin(1023) {
			width: 840px;
			position: relative;
			margin-top: 20px; }

		@include rmin(675) {
			width: 640px;
			margin-top: 10px; }

		@include rmin(321) {
			width: 320px; } } }

.h3 {
	font-family: HelveticaNeue_Light, sans-serif;
	font-size: 12px;
	color: $ORANGE;
	text-align: center; }

.h1__grey {
	margin-top: 10px;
	letter-spacing: 0.3px;
	color: $SPACE_GREY;
	text-align: center; }

.h5 {
	padding-top: 5px;
	margin-top: 20px;
	font-family: HelveticaNeue, sans-serif;
	font-size: 13px;
	color: $SPACE_GREY; }

.grid {
	display: grid;
	text-align: center;
	width: 100%;


	@include rmin(1023) {

		grid-template-areas: "grid-item grid-item grid-item" "grid-item grid-item grid-item";
		grid-row-gap: 55px;
		margin-top: 55px;
		grid-template-columns: 250px 250px 250px;
		grid-column-gap: 30px;
		padding-left: 10px;
		padding-right: 10px; }

	@include rmin(675) {

		grid-template-areas: "grid-item grid-item grid-item" "grid-item grid-item grid-item";
		grid-row-gap: 70px;
		grid-column-gap: 40px;
		grid-template-columns: 180px 180px 180px;
		margin-top: 35px;
		padding-left: 10px;
		padding-right: 10px; }

	@include rmin(321) {

		grid-template-areas: "first_item second_item" "third_item fourth_item" "fifth_item sixth_item";
		grid-row-gap: 40px;
		grid-column-gap: 20px;
		grid-template-columns: 140px 140px;
		padding-left: 10px;
		padding-right: 10px;
		margin-top: 25px; }

	&-item {

		&__logo {
			height: auto;

			@include rmin(1023) {
				width: 105px; }

			@include rmin(675) {
				width: 75px; }

			@include rmin(321) {
				width: 65px; } }

		&__description {
			padding-top: 15px;
			font-family: HelveticaNeue_Medium, sans-serif;
			font-size: 12.5px;
			color: $GREY; } } }
