$WHITE: #FFFFFF;
$RED: #FC6644;

.header {
	width: 100%;
	background-image: url("../img/header_bg.jpg");
	background-repeat: no-repeat;

	@include rmin(1023) {
		height: 630px; }

	@include rmin(675) {
		height: 450px; }

	@include rmin(321) {
		height: 530px; }


	&-container {
		height: inherit;
		position: relative;
		margin: 0 auto;
		padding-top: 0px;
		padding-right: 10px;
		padding-bottom: 0px;
		padding-left: 10px;

		@include rmin(1023) {
			width: 870px; }

		@include rmin(675) {
			width: 640px; }

		@include rmin(321) {
			width: 320px; } }

	&-navbar__logo {
		float: left;

		width: 35px;
		height: auto;

		&:hover {
			opacity: 0.5; }

		@include rmin(1023) {
			margin-left: 5px;
			padding-top: 15px; }

		@include rmin(675) {
			margin-left: 25px;
			padding-top: 15px; }

		@include rmin(321) {
			margin-left: 5px;
			padding-top: 10px; } } }

.list {
	margin: 0;
	padding-left: 0px;
	list-style: none;

	@include rmin(1023) {
		margin-left: 270px;
		padding-top: 35px; }

	@include rmin(675) {
		margin-left: 70px;
		padding-top: 35px; }

	@include rmin(321) {
		margin-left: 30px;
		padding-top: 15px; }

	&-item {
		float: left;

		@include rmin(1023) {
			margin-left: 37px; }

		@include rmin(675) {
			margin-left: 30px; }

		@include rmin(321) {
			margin-left: 25px; }

		&__link {
			font-family: HelveticaNeue, sans-serif;
			font-size: 14px;
			color: $WHITE;
			text-decoration: none;

			&:hover {
				opacity: 0.5; } } } }

.h1 {
	font-family: HelveticaNeue_Medium, sans-serif;
	letter-spacing: 0.575px;
	color: $WHITE;

	@include rmin(1023) {
		font-size: 25px; }

	@include rmin(675) {
		font-size: 20px; }

	@include rmin(321) {
		font-size: 15px; } }

.article {
	position: absolute;

	&__header {
		@include rmin(1023) {
			width: 420px;
			left: 15px;
			top: 220px;
			text-align: left; }

		@include rmin(675) {
			width: 300px;
			top: 100px;
			text-align: center; }

		@include rmin(321) {
			width: 250px;
			top: 285px;
			left: 35px;
			text-align: center; } }

	&__text {
		font-family: HelveticaNeue, sans-serif;
		color: $WHITE;
		line-height: 25px;
		letter-spacing: 0.09px;
		margin-top: 27px;

		@include rmin(1023) {
			font-size: 14px; }

		@include rmin(675) {
			font-size: 14px; }

		@include rmin(321) {
			font-size: 12px; } } }

.button {
	position: absolute;

	@include rmin(1023) {
		top: 430px;
		left: 17px; }

	@include rmin(675) {
		top: 330px;
		left: 100px; }

	@include rmin(321) {
		top: 470px;
		left: 75px; }

	&__link {
		padding-top: 14px;
		padding-right: 38px;
		padding-bottom: 12px;
		padding-left: 38px;
		border: 1px solid white;
		border-radius: 3px;
		font-family: HelveticaNeue_Light, sans-serif;
		font-size: 14px;
		color: $WHITE;
		text-decoration: none;

		&:hover {
			background-color: $WHITE;
			color: $RED; } } }

.main-image {
	position: absolute;

	&__iphone {
		height: auto;

		@include rmin(1023) {
			width: 210px; }

		@include rmin(675) {
			width: 150px; }

		@include rmin(321) {
			width: 100px; } }

	@include rmin(1023) {
		top: 140px;
		left: 630px; }

	@include rmin(675) {
		top: 95px;
		left: 450px; }

	@include rmin(321) {
		top: 70px;
		left: 110px; } }
