$RED: #fe8435;
$WHITE: #ffffff;

.join {
	width: 100%;
	background-image: url("../img/join_bg.jpg");
	background-repeat: no-repeat;
	background-size: cover;

	@include rmin(1023) {
		height: 400px; }

	@include rmin(675) {
		height: 335px; }

	@include rmin(321) {
		height: 330px; }

	&-container {
		margin: 0 auto;

		@include rmin(1023) {
			width: 840px;
			padding-top: 70px; }

		@include rmin(675) {
			width: 640px;
			height: inherit;
			padding-top: 50px; }

		@include rmin(321) {
			width: 320px;
			height: inherit;
			padding-top: 25px;
			padding-left: 10px;
			padding-right: 10px; } } }

.article {
	&__center {
		text-align: center;
		padding-top: 31px;

		@include rmin(1023) {
			width: 820px; }

		@include rmin(675) {
			width: 620px; }

		@include rmin(321) {
			width: 300px; } }

	&__text__bottom {
		margin-top: 10px;
		letter-spacing: 0.175px;
		font-family: HelveticaNeue, sans-serif;

		@include rmin(1023) {
			font-size: 12.5px; }

		@include rmin(675) {
			font-size: 12.5px; }

		@include rmin(321) {
			font-size: 12.5px; } } }


.h1_small {
	letter-spacing: 4.1px;

	@include rmin(1023) {
		font-size: 20px; }

	@include rmin(675) {
		font-size: 20px; }

	@include rmin(321) {
		font-size: 15px; } }


.feed-back {
	margin-top: 65px;

	@include rmin(1023) {
		text-align: center; }

	@include rmin(675) {
		position: relative; }

	@include rmin(321) {
		position: relative; }

	&__email {
		padding: 14px;
		background-color: rgba(255, 255, 255, 0);
		border: 1px solid $WHITE;
		border-radius: 3px;
		color: $WHITE;
		position: absolute;

		@include rmin(1023) {
			width: 425px;
			top: 113px;
			left: 110px;
			text-align: left; }

		@include rmin(675) {
			width: 400px;
			top: 75px;
			left: 100px;
			text-align: center; }

		@include rmin(321) {
			width: 270px;
			top: 105px;
			left: 15px;
			text-align: center; } }

	&__button {
		padding-top: 15px;
		padding-right: 31px;
		padding-bottom: 15px;
		padding-left: 31px;
		background-color: $WHITE;
		border: 1px solid $WHITE;
		border-radius: 3px;
		position: absolute;

		@include rmin(1023) {
			top: 113px;
			left: 555px; }

		@include rmin(675) {
			top: 140px;
			left: 230px; }

		@include rmin(321) {
			top: 170px;
			left: 86.5px; }

		&:hover {
			opacity: 0.5; }

		&__name {
			font-family: HelveticaNeue_Medium, sans-serif;
			font-size: 12px;
			color: $RED;
			text-align: center;
			letter-spacing: 1px; } } }
